/*
 * Typography
 */
:root {
  --base-color: #67c0c1;
  --base-color-darker: #409b9c;
  --base-bg-color: #f6f7f9;
  --base-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  --base-font-size: 16px;
  --base-font-weight: 300;
  --base-letter-spacing: 0em;
  --content-width: 960px;
  --content-width-wide: 1060px;
  --headings-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  --headings-font-weight: 600;
  --headings-letter-spacing: 0em;
  --base-border-radius: 3rem;
  --footer-height: 6rem; }

/*
 * Typography
 */
.v-align {
  height: 100%;
  display: block;
  white-space: nowrap; }
  .v-align:after {
    content: "";
    height: 100%;
    vertical-align: middle;
    width: 0;
    display: inline-block; }
  .v-align > div {
    display: inline-block;
    white-space: normal;
    vertical-align: middle;
    width: 100%; }

.v-align-middle {
  vertical-align: middle; }

.v-align-top {
  vertical-align: top !important; }

.v-align-bottom {
  vertical-align: bottom !important; }

.text-center {
  text-align: center; }

.aligncenter {
  max-width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.aligncontainer {
  width: var(--content-width, 960px);
  max-width: 100%;
  margin-left: auto;
  margin-right: auto; }

.alignfull,
.alignwide {
  margin-left: calc( -100vw / 2 + 100% / 2);
  margin-right: calc( -100vw / 2 + 100% / 2);
  max-width: 100vw;
  width: auto; }

@media only screen and (min-width: 960px) {
  .alignwide {
    margin-left: -125px;
    margin-right: -125px; } }

