.aspect-ratio {
  position: relative; }
  .aspect-ratio iframe, .aspect-ratio object {
    width: 100%;
    height: 100%; }
  .aspect-ratio.ratio-custom {
    position: relative;
    overflow: visible;
    display: block;
    padding-top: 56.25%; }
    .aspect-ratio.ratio-custom .force-width {
      width: 100%; }
    .aspect-ratio.ratio-custom img {
      min-width: 100%;
      max-width: 100%;
      height: auto; }
    .aspect-ratio.ratio-custom:before {
      content: none; }
  .aspect-ratio:before {
    display: block;
    content: "";
    width: 100%;
    padding-top: 56.25%; }
  .aspect-ratio.ratio-3-4 {
    overflow: visible; }
    .aspect-ratio.ratio-3-4:before {
      padding-top: 75%; }
  .aspect-ratio.ratio-square {
    overflow: visible; }
    .aspect-ratio.ratio-square:before {
      padding-top: 100%; }
  .aspect-ratio img, .aspect-ratio iframe, .aspect-ratio object {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }

